.searchPage_header {
    display: flex;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 20px;
    height: 50%;
    border: 1px solid grey;
}

.searchPage_logo {
    object-fit: contain;
    height: 50px;
    margin-right: 50px;
}

.searchPage_headerBody>.search>.search_input {
    margin-top: 0;
    width: unset;
    height: unset;
    max-width: unset;
}

.searchPage_Options {
    display: flex;
    align-items: center;
    color: grey;
}

.searchPage_option {
    display: flex;
    align-items: center;
    margin-right: 20px;
}

.searchPage_option a {
    margin-left: 5px;
}

.searchPage_OptionsLeft, .searchPage_OptionsRight {
    display: flex;
}

.searchPage_OptionsLeft a, .searchPage_OptionsRight a {
    text-decoration: none;
    color: gray;
}

.searchPage_OptionsRight {
    margin-left: 80px;
}

.searchPage_result {
    margin: 40px 0;
}

.searchPage_resultCount {
    color: #70757a;

    font-size: 24px;
}

.searchPage_results {
    max-width: 650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;
}
.searchPage_resultTitle{
    text-decoration: none;
}
.searchPage_resultTitle:hover{
    text-decoration: underline;
}
.searchPage_resultTitle>h2{
    font-weight: 500;
}
.searchPage_resultImage{
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-right: 10px;
}